.panel {
  display: flex;
  padding: 30px;
  box-shadow: var(--box-shadow);
  border-radius: var(--border-radius);
}

.actions {
  flex: 1;

  ul {
    overflow: hidden;
  }

  li {
    min-height: 40px;
    line-height: 36px;
    padding-left: 70px;
    position: relative;
    overflow: hidden;
  }

  .label {
    padding-left: 30px;
    position: absolute;
    left: 0;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 5px;
      width: 30px;
      height: 30px;
      background-repeat: no-repeat;
      background-position: left center;
    }
  }

  .item {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
  }

  .text {
    margin: 0 15px;
  }

  .hyphen {
    margin: 0 6px;
    color: var(--text-light-color);
  }

  .sort {
    .item {
      position: relative;
    }

    span {
      cursor: pointer;
    }

    .cursor {
      position: absolute;
      left: 0;
      top: 3px;
      width: 86px;
      height: 30px;
      border-radius: 30px;
      background-color: var(--primary-color);
      z-index: -1;
      transition: left 0.3s;
    }

    .active {
      color: #fff;
    }

    .label::before {
      background-image: url(../../../../assets/images/sort.png);
    }
  }

  .filter {
    .label::before {
      background-image: url(../../../../assets/images/filter.png);
    }
  }

  .search {
    .label::before {
      background-image: url(../../../../assets/images/search.png);
    }
  }

  // 按钮们
  .buttons {
    button {
      margin-right: 30px;
    }
  }
}

// 图表
.charts {
  display: flex;
  width: 450px;
  padding-top: 24px;

  .today,
  .total {
    width: 50%;
    padding-left: 40px;
  }

  h5 {
    font-size: 12px;
    font-weight: normal;
    margin-bottom: 10px;
  }

  strong {
    line-height: 1;
    font-size: 32px;
  }

  .chart {
    width: 180px;
    height: 120px;
    margin-top: 20px;
  }
}

@media screen and (max-width: 1279px) {
  .charts {
    display: none;
  }
}
